<template>
  <div class="contactus">
    <!-- NAVBAR -->
    <navbar></navbar>
    <a href="https://www.google.com/maps/place/De+Franco+Dental/@32.7160919,-114.7297797,15z/data=!4m5!3m4!1s0x0:0x3f46dbd67b2624da!8m2!3d32.7160919!4d-114.7297797">
      <img id="mapDFranco" src="../assets/mapfranco.jpg" alt="mapDeFrancoDental">
    </a>
    <!-- TABLE INFORMATION DENTAL -->
    <div style="background-color: #6F51C7;">
      <br>
    </div>
    <br>
    <!-- TITLE CONTACT US FOR A CONSULTATION -->
    <h3>
      <b>CONTACT US FOR A CONSULTATION</b>
    </h3>
    <br> <br>
    <!-- FORM CONTACT US FOR SERVICES -->
    <b-container class="consultation-input">
      <b-row>
        <b-col></b-col>
        <b-col>
          <b-form-input 
              v-model="name" 
              placeholder="Name"
              type="text"
              required
              >
          </b-form-input>
        </b-col>
        <b-col>
          <b-form-input 
              v-model="lastname" 
              placeholder="Last Name"
              type="text"
              required
          >
          </b-form-input>
        </b-col>
        <b-col></b-col>
      </b-row>
    </b-container>
    <br>
    <b-container class="consultation-input">
      <b-row>
        <b-col></b-col>
        <b-col>
          <b-form-input 
              v-model="email" 
              type="email" 
              placeholder="E-mail"
              required
              >
          </b-form-input>
        </b-col>
        <b-col>
          <b-form-input 
              v-model="phone" 
              type="number" 
              placeholder="Phone"
              required
              >
          </b-form-input>
        </b-col>
        <b-col></b-col>
      </b-row>
    </b-container>
    <br>
    <b-container class="consultation-input">
      <b-row>
        <b-col></b-col>
        <b-col>
          <b-form-textarea
            id="textarea"
            v-model="message"
            placeholder="Message"
            rows="3"
            max-rows="6"
            type="text"
            required
          ></b-form-textarea>
        </b-col>
        <b-col>
       <b-form-select v-model="selected" :options="options"></b-form-select>
          <br> <br>
          <b-button pill block v-b-modal.modal-1 class="btn-send" type="submit" @click="addtoAPI">Send</b-button>
            <b-modal id="modal-1" title="Hey Hello!">
              <p id="text-modal"><img id="tooth-modal" src="../assets/010-approve.png" alt="">Your information has been sent, we will respond as soon as possible.</p>
            </b-modal>
          <!--modalfalse/-->
        </b-col>
        <b-col></b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import navbar from '../components/navbar'
/*import modalfalse from '../components/ModalFalse'*/


export default {
  components: {
    navbar,
    /*modalfalse*/
  },
  data() {
      return {
          name: '',
          lastname: '',
          phone: '',
          email: '',
          message: '',
          selected: null,
          options: [
            { value: null, text: 'I’m interested in service' },
            { value: 'Dental Implants', text: 'Dental Implants' },
            { value: 'Braces', text: 'Braces' },
            { value: 'Crowns and Bridges', text: 'Crowns and Bridges' },
            { value: 'Endodontics', text: 'Endodontics' },
            { value: 'Fillings and Extractions', text: 'Fillings and Extractions' },            
          ]
        }
    },
    methods: {
      addtoAPI(){
        this.axios.post('http://localhost:3000/email',{

            name:this.name,
            email:this.email,
            text:this.message,
            ln:this.lastname,
            phone:this.phone,
            service: this.selected

          })
        .then(res=> {
          console.log('Acción realizada con éxito'+res.data);
        })
        .catch((error) => {
          console.log('[es esete errorr!!!]'+error.response);
        })
      }
    }
}
</script>

