@import url('https://fonts.googleapis.com/css?family=Lato|Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

  .contactus{
    font-family: 'Montserrat';
  }

  .consultation-input{
    font-family: 'Lato';
  }

/* MODAL TRUE */
  
  .btn-send{
    background: #6F51C7;
    border: none;
    margin-bottom: 100px;
  }
  .btn-send:hover{
    background: #6F51C7;
  }

  #tooth-modal{
    margin-left: 10px;
    width: 44px;
    height: 44px;
  }

  #text-modal{
    font-family: 'Montserrat';
  }
  
  #mapDFranco{
    width: 100%;
    max-height: 100%;
    margin-top: 49px;
  }
</style>